  width: 15px;
  height: 15px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-left: 5px;
  margin-right: 5px;
}

@keyframes scroll {
  0% {
    transform: translate(0);
  }

  100% {
    transform: translate(-100%);
  }
}

.marquee {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
}

.marquee span {
  animation: scroll 6s linear infinite;
}

