  border: 1px solid #2d2a26;
  border-radius: 10px;
  margin-left: auto;
  padding: 16px 27px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  box-shadow: 4px 4px #2d2a26;
}

.btn-nav__get-kit:hover {
  transform: scale(.9)translateX(-7px);
}

